  margin-bottom: 5px; }

.DialogIMG-btn:hover,
.DialogIMG-btn:focus {
  /* -webkit-box-shadow: inset 0px 0px 3px 2px rgba(233,233,233,1);
    -moz-box-shadow: inset 0px 0px 3px 2px rgba(233,233,233,1);
    box-shadow: inset 0px 0px 3px 2px rgb(233, 233, 233); */
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  border-bottom: 4px ridge white; }

@-webkit-keyframes example {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-bottom: 4px ridge transparent; }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-bottom: 4px ridge white; } }

@keyframes example {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-bottom: 4px ridge transparent; }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-bottom: 4px ridge white; } }

/* Container needed to position the overlay. Adjust the width as needed */
.container1 {
  position: relative;
  width: 150px !important;
  height: 150px !important; }

.feed {
  -webkit-transition: 0.3 ease;
  transition: 0.3 ease;
  overflow-y: scroll;
  position: relative;
  height: 80vh;
  scroll-behavior: initial; }

/* Make the image to responsive */
/* The overlay effect (full height and width) - lays on top of the container and over the image */
.overlay1 {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 150px !important;
  height: 150px !important;
  opacity: 0;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  background-color: rgba(0, 0, 0, 0.192);
  border-radius: 50%; }

/* When you mouse over the container, fade in the overlay icon*/
.container1:hover .overlay1 {
  opacity: 1; }

.icon1 {
  width: 150px !important;
  height: 150px !important;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center; }

.icon1 svg {
  color: white; }

.MuiSvgIcon-colorPrimary:hover {
